import { ChangeEvent, Fragment, useCallback, useEffect, useState } from "react";
import { Helmet } from "react-helmet-async";
import { Link } from "react-router-dom";
import { Card, CardContent, Divider, IconButton } from "@mui/material";
import { Clear } from "@mui/icons-material";

import BizParticipantsApi from "Apis/BizParticipantsApi";
import { useGlobal } from "Contexts/App";
import { useSnacks } from "Contexts/Snack";
import {
  useBizParticipantsApi,
  useBizParticipantsData,
} from "Contexts/BizParticipants";
import { ActionButton } from "SharedComponents/CommonComponents";
import { LeftArrow, Search } from "Assets/Icons";
import {
  CardSX,
  CardContentSX,
  Content,
  ContentInner,
  InnerWrapper,
  ContentHeader,
  HeaderTitle,
  InnerBlock,
  Cta,
  CtaInfo,
  CtaInfoText,
  SearchWrapper,
  SearchBlock,
  SearchInput,
  InnerWrapperSX,
  ContentFooter,
  ContentInnerSX,
  ContentHeaderSX,
} from "Containers/BusinessSettings/LayoutComponents";
import NewMemberInvite from "./TeamMembers/NewMemberInvite";
import TeamHeader from "./TeamMembers/TeamHeader";
import TeamMemberItem from "./TeamMembers/TeamMemberItem";

const TeamMembers = () => {
  const { isMobile, isTablet } = useGlobal();
  const { setErrorMessage } = useSnacks();
  const [loading, setLoading] = useState<boolean>(true);
  const [isSearchActive, setSearchActive] = useState<boolean>(false);
  const { invitedMembers, showInviteForm, invitedMemberFilter } =
    useBizParticipantsData();
  const { setInvitedMembers, setShowInviteForm, setInvitedMemberFilter } =
    useBizParticipantsApi();
  const fetchTeamMembers = useCallback(async () => {
    try {
      const res = await BizParticipantsApi.getUserInvites();
      if (!res.success) {
        throw Error(res.message);
      }
      setInvitedMembers(res.payload);
      setLoading(false);
    } catch (error: any) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    fetchTeamMembers();
  }, [fetchTeamMembers]);

  return (
    <Fragment>
      <Helmet>
        <title>Team Members</title>
      </Helmet>
      <Content>
        {isMobile && (
          <ContentHeader sx={ContentHeaderSX}>
            {!isSearchActive && (
              <IconButton
                component={Link}
                to="/business-settings"
                sx={{ width: 32, height: 32 }}
              >
                <LeftArrow />
              </IconButton>
            )}
            {!isSearchActive && <HeaderTitle>Team Members</HeaderTitle>}
            {isSearchActive && (
              <SearchWrapper>
                <SearchBlock>
                  <IconButton
                    disableTouchRipple
                    disableFocusRipple
                    disableRipple
                    sx={{ position: "absolute" }}
                  >
                    <Search />
                  </IconButton>
                  <SearchInput
                    onChange={({
                      target: { value },
                    }: ChangeEvent<HTMLInputElement>) =>
                      setInvitedMemberFilter(value)
                    }
                    placeholder="Search People"
                  />
                  <IconButton
                    onClick={() => {
                      setInvitedMemberFilter("");
                      setSearchActive(!isSearchActive);
                    }}
                    sx={{ position: "absolute", right: 0, top: 0 }}
                  >
                    <Clear />
                  </IconButton>
                </SearchBlock>
              </SearchWrapper>
            )}
            {!isSearchActive && invitedMembers.length > 0 && (
              <IconButton
                onClick={() => setSearchActive((prevState) => !prevState)}
              >
                <Search />
              </IconButton>
            )}
          </ContentHeader>
        )}
        <ContentInner sx={ContentInnerSX}>
          <InnerWrapper sx={InnerWrapperSX}>
            {!loading && (
              <Card elevation={0} sx={CardSX}>
                <CardContent sx={CardContentSX}>
                  {invitedMembers.length === 0 ? (
                    <InnerBlock>
                      <Cta>
                        <ActionButton
                          disableIcon
                          onClick={() => setShowInviteForm(true)}
                          sx={{
                            borderRadius: 2,
                            boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
                            fontSize: 16,
                            fontWeight: 700,
                            height: 36,
                            lineHeight: "19px",
                            margin: "auto",
                            textTransform: "capitalize",
                          }}
                        >
                          {"Invite Team Member"}
                        </ActionButton>
                      </Cta>
                      <CtaInfo>
                        <CtaInfoText>
                          {
                            "You donâ€™t have any team members yet. Invite a team member and start working in collaboration."
                          }
                        </CtaInfoText>
                      </CtaInfo>
                    </InnerBlock>
                  ) : (
                    <Fragment>
                      {isTablet && (
                        <TeamHeader {...{ isSearchActive, setSearchActive }} />
                      )}
                      {isTablet && <Divider />}
                      {invitedMembers
                        .filter((element: any) =>
                          element?.name
                            ?.toLowerCase()
                            .includes(invitedMemberFilter?.toLowerCase())
                        )
                        .map(
                          (
                            {
                              name,
                              status,
                              id,
                            }: { name: string; status: string; id: number },
                            i: number
                          ) => {
                            return (
                              <TeamMemberItem
                                key={i}
                                title={name}
                                description={status}
                                invitationId={id}
                                status={status}
                              />
                            );
                          }
                        )}
                    </Fragment>
                  )}
                </CardContent>
              </Card>
            )}
          </InnerWrapper>
        </ContentInner>
        {isMobile && invitedMembers.length > 0 && (
          <ContentFooter>
            <ActionButton
              onClick={() => setShowInviteForm(true)}
              variant="outlined"
              color="primary"
              disableIcon
            >
              Invite Team Member
            </ActionButton>
          </ContentFooter>
        )}
      </Content>
      <NewMemberInvite
        open={showInviteForm}
        closeDrawer={() => setShowInviteForm(false)}
      />
    </Fragment>
  );
};

export default TeamMembers;
