import { ChangeEvent, Dispatch, SetStateAction } from "react";
import { IconButton } from "@mui/material";
import { Clear } from "@mui/icons-material";

import { ActionButton } from "SharedComponents/CommonComponents";
import { Search } from "Assets/Icons";
import { useBizParticipantsApi } from "Contexts/BizParticipants";
import {
  SearchBlock,
  SearchInput,
  SearchWrapper,
  TeamHead,
  TeamHeadBottom,
  TeamHeadBottomLeft,
  TeamHeadBottomRight,
  TeamHeadTitle,
  TeamHeadTop,
} from "Containers/BusinessSettings/LayoutComponents";

const TeamHeader = ({
  isSearchActive,
  setSearchActive,
}: {
  isSearchActive: boolean;
  setSearchActive: Dispatch<SetStateAction<boolean>>;
}) => {
  const {
    // ? for form toggle
    setShowInviteForm,
    // ? for filter value
    setInvitedMemberFilter,
  } = useBizParticipantsApi();

  return (
    <TeamHead>
      <TeamHeadTop>
        <ActionButton
          disableIcon
          type="button"
          onClick={() => setShowInviteForm(true)}
          sx={{ py: 0, px: 0.625, height: 36, mt: 0 }}
        >
          {"Invite Team Member"}
        </ActionButton>
      </TeamHeadTop>
      <TeamHeadBottom>
        {!isSearchActive && (
          <TeamHeadBottomLeft>
            <TeamHeadTitle>Team Members</TeamHeadTitle>
          </TeamHeadBottomLeft>
        )}
        <TeamHeadBottomRight>
          {isSearchActive && (
            <SearchWrapper>
              <SearchBlock>
                <IconButton
                  disableTouchRipple
                  disableFocusRipple
                  disableRipple
                  sx={{ position: "absolute" }}
                >
                  <Search />
                </IconButton>
                <SearchInput
                  onChange={({
                    target: { value },
                  }: ChangeEvent<HTMLInputElement>) =>
                    setInvitedMemberFilter(value)
                  }
                  placeholder="Search People"
                />
                <IconButton
                  onClick={() => {
                    setInvitedMemberFilter("");
                    setSearchActive(!isSearchActive);
                  }}
                  sx={{ position: "absolute", right: 0, top: 0 }}
                >
                  <Clear />
                </IconButton>
              </SearchBlock>
            </SearchWrapper>
          )}
          {!isSearchActive && (
            <IconButton
              onClick={() => setSearchActive((prevState) => !prevState)}
            >
              <Search />
            </IconButton>
          )}
        </TeamHeadBottomRight>
      </TeamHeadBottom>
    </TeamHead>
  );
};

export default TeamHeader;
