import { AuthenticatedReq } from "Utils/Axios";

export default class BizParticipantsApi {
  public static getTeamMembers = async (
    search: string = "",
    pageNo: number = 1,
    limit: number = 20
  ) => {
    return await AuthenticatedReq(
      `/team-members?search=${search}&page=${pageNo}&limit=${limit}`,
      "GET"
    );
  };

  public static getUserInvites = async () => {
    return await AuthenticatedReq(`/invitation/all`, "GET");
  };

  public static sendUserInvite = async (data: any) => {
    return await AuthenticatedReq(`/invitation`, "POST", { data });
  };

  public static deleteUserInvite = async (invitationId: number) => {
    return await AuthenticatedReq(`/invitation/${invitationId}`, "DELETE");
  };
}
