import { Fragment, MouseEvent, useState } from "react";
import {
  Menu,
  MenuItem,
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
  Button,
  ListItem,
  ListItemText,
  ListItemIcon,
  IconButton,
} from "@mui/material";
import { MoreVert } from "@mui/icons-material";

import { useSnacks } from "Contexts/Snack";
import BizParticipantsApi from "Apis/BizParticipantsApi";

const TeamMemberItem = (props: any) => {
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { title, description, invitationId, status } = props;
  // 3dot menu state
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);

  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);

  //function for opening confirm box for delete
  const handleClickOpen = () => {
    setOpenConfirmDialog(true);
  };
  //function for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };
  //function for opening 3 dot menu
  const handleClick = (event: MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  //function for closing 3 dot menu
  const handleClose = () => {
    setAnchorEl(null);
  };

  const deleteMember = async () => {
    try {
      const res = await BizParticipantsApi.deleteUserInvite(invitationId);
      if (!res.success) {
        throw Error(res.message);
      }
      setSuccessMessage(res.payload.message);
      setTimeout(() => window.location.reload(), 1000);
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      handleClickClose();
    }
  };

  // const resendInvitation = async () => {
  //   const res = await resendInvitationMail(invitationId);
  //   if (res.error) {
  //     return setErrorMessage(res.error);
  //   } else {
  //     return setSuccessMessage(res.success);
  //   }
  // };

  return (
    <Fragment>
      <ListItem component="div" sx={{ px: { xs: 0, md: 2 } }}>
        <ListItemText
          primary={
            <div className="primary-block">
              <h2 className={"list-title"}>{title}</h2>
            </div>
          }
          secondary={description}
          sx={{
            "& .primary-block": {
              alignItems: "center",
              display: "flex",
              flexWrap: "wrap",
              gap: 10,
              marginBottom: 0.75,

              "& .list-title": {
                color: "text.secondary",
                fontSize: 15,
                fontWeight: 500,
                lineHeight: "18px",
              },
            },

            "& .MuiListItemText-secondary": {
              color: "#767676b3",
              fontSize: 10,
              maxWidth: 290,
              fontWeight: 400,
              lineHeight: "15px",
              textTransform: "capitalize",
            },
          }}
        />
        <ListItemIcon>
          <IconButton
            // disabled
            onClick={handleClick}
          >
            <MoreVert />
          </IconButton>
        </ListItemIcon>
      </ListItem>
      {Boolean(anchorEl) && (
        <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={handleClose}
          keepMounted
          MenuListProps={{
            "aria-labelledby": "Options",
          }}
          anchorOrigin={{
            vertical: "top",
            horizontal: "right",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "right",
          }}
          sx={{
            "& .MuiPaper-root": { minWidth: 120 },
          }}
        >
          <MenuItem
            onClick={() => {
              handleClickOpen();
              handleClose();
            }}
          >
            {"Delete"}
          </MenuItem>
          {!status && (
            <MenuItem
              onClick={() => {
                // resendInvitation();
                handleClose();
              }}
            >
              {"Resend"}
            </MenuItem>
          )}
        </Menu>
      )}
      <Dialog
        open={openConfirmDialog}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Delete"}</DialogTitle>
        <DialogContent>{"Are you sure you want to delete?"}</DialogContent>
        <DialogActions>
          <Button
            color="secondary"
            onClick={() => {
              handleClickClose();
            }}
          >
            {"Cancel"}
          </Button>
          <Button
            onClick={async () => {
              await deleteMember();
            }}
            autoFocus
          >
            {"Delete"}
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default TeamMemberItem;
