import { createContext, FC, useContext, useMemo, useState } from "react";

import {
  InvitedMemberInterface,
  TeamMemberInterface,
} from "Shared/Interfaces/BizParticipants";

import {
  BizParticipantsApiContextState,
  BizParticipantsDataContextState,
} from "./types";

const BizParticipantsData = createContext<
  BizParticipantsDataContextState | undefined
>(undefined);

const BizParticipantsApi = createContext<
  BizParticipantsApiContextState | undefined
>(undefined);

const BizParticipantsProvider: FC<any> = ({ children }) => {
  const [showInviteForm, setShowInviteForm] = useState<boolean>(false);
  const [invitedMembers, setInvitedMembers] = useState<
    InvitedMemberInterface[]
  >([]);
  const [invitedMemberFilter, setInvitedMemberFilter] = useState<string>("");
  const [teamMembers, setTeamMembers] = useState<TeamMemberInterface[]>([]);
  const [teamMembersTotal, setTeamMembersTotal] = useState<
    number | undefined
  >();
  const data: BizParticipantsDataContextState = {
    showInviteForm,
    invitedMembers,
    invitedMemberFilter,
    teamMembers,
    teamMembersTotal,
  };
  const api = useMemo<BizParticipantsApiContextState>(
    () => ({
      setShowInviteForm,
      setInvitedMembers,
      setTeamMembers,
      setInvitedMemberFilter,
      setTeamMembersTotal,
    }),
    []
  );

  return (
    <BizParticipantsData.Provider value={data}>
      <BizParticipantsApi.Provider value={api}>
        {children}
      </BizParticipantsApi.Provider>
    </BizParticipantsData.Provider>
  );
};

export const useBizParticipantsData = () => {
  const context = useContext(BizParticipantsData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of BizParticipantsProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export const useBizParticipantsApi = () => {
  const context = useContext(BizParticipantsApi);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of BizParticipantsProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default BizParticipantsProvider;
