import { Drawer, IconButton } from "@mui/material";
import { useForm } from "react-hook-form";
import { MuiTelInput, MuiTelInputInfo } from "mui-tel-input";

import { ActionArea, ActionButton } from "SharedComponents/CommonComponents";
import { useSnacks } from "Contexts/Snack";
import { isBackDropOrEscapeKey } from "Utils";
import { LeftArrow } from "Assets/Icons";
import {
  ContentHeader,
  DrawerForm,
  DrawerWrapper,
  Field,
  FlexRow,
  FormTelFieldSX,
  FormTextFieldSX,
  GrowColumn,
  HeaderTitle,
} from "Containers/BusinessSettings/LayoutComponents";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import BizParticipantsApi from "Apis/BizParticipantsApi";

interface NewMemberInviteProps {
  open: boolean;
  closeDrawer: () => void;
}

const NewMemberInvite = ({ open, closeDrawer }: NewMemberInviteProps) => {
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { control, handleSubmit, watch, setValue } = useForm({
    defaultValues: {
      name: "",
      number: "",
      email: "",
      plan_category: ["engage"],
    },
  });
  const { number } = watch();
  const onSubmit = async (data: any) => {
    try {
      const res = await BizParticipantsApi.sendUserInvite(data);
      if (!res.success) {
        throw Error(res.message);
      }
      setSuccessMessage(res.message);
      setTimeout(() => window.location.reload(), 1000);
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      closeDrawer();
    }
  };

  return (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={(_, reason) => !isBackDropOrEscapeKey(reason) && closeDrawer()}
      sx={{ zIndex: "modal" }}
    >
      <DrawerWrapper sx={{ width: { xs: "100vw", md: 360 } }}>
        <ContentHeader>
          <IconButton onClick={closeDrawer} sx={{ width: 32, height: 32 }}>
            <LeftArrow />
          </IconButton>
          <HeaderTitle>Invite a Team Member</HeaderTitle>
        </ContentHeader>
        <DrawerForm onSubmit={handleSubmit(onSubmit)}>
          <FlexRow>
            <GrowColumn>
              <Field>
                <FormTextField
                  control={control}
                  name="name"
                  label="Name"
                  placeholder="Name"
                  required={true}
                  sx={FormTextFieldSX}
                />
              </Field>
              <Field>
                <FormTextField
                  control={control}
                  name="email"
                  label="Email"
                  placeholder="Email"
                  required={true}
                  sx={FormTextFieldSX}
                />
              </Field>
              <Field>
                <MuiTelInput
                  label={"Mobile"}
                  placeholder={"Enter mobile no."}
                  fullWidth={true}
                  defaultCountry="IN"
                  value={number}
                  onChange={(newValue: string, info: MuiTelInputInfo) => {
                    setValue("number", info.numberValue ?? newValue);
                    console.log(info.numberValue);
                  }}
                  variant={"filled"}
                  sx={FormTelFieldSX}
                />
              </Field>
            </GrowColumn>
          </FlexRow>
          <ActionArea>
            <ActionButton disableIcon color="secondary" onClick={closeDrawer}>
              Cancel
            </ActionButton>
            <ActionButton type="submit">Send Invite</ActionButton>
          </ActionArea>
        </DrawerForm>
      </DrawerWrapper>
    </Drawer>
  );
};

export default NewMemberInvite;
